.form__theme-dark {
	margin: auto;
	height: calc(100dvh - 264px);
	color: #FFFFFF;
	background-color: unset;
}

@media screen and (max-width: 918px) {
	.form__theme-dark {
		height: calc(100dvh - 200px);
	}
}

@media screen and (max-width: 619px) {
	.form__theme-dark {
		width: 304px;
	}
}